@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



body{ 
  background-image: url(/codetracker2020/static/media/bg.db29b234.png);
  
}

.App {
  margin-left: 0vh;
  margin-top: -5vh;
  margin-bottom: 0vh;
  background-image: url(/codetracker2020/static/media/bg.db29b234.png);
  min-width: 100vw;
  opacity: 0.1;
}
h1{
  color: red;
  text-align:center;
  margin-top: -2rem;
  text-shadow: -3px 0px  orange, 1px 0 #ddd, 0 3px lightblue;
  font-size: 3rem;
  font-family: 'Orbitron', sans-serif;
  transform: rotate(-0.2deg);
  padding: 5vh;
}

h4{
  text-align: center;
  color: rgba(00,0,0,0.8);
  margin-top: -10vh;
  font-family: 'Orbitron', sans-serif;
  text-shadow: 1px 1px  1px rgba(200,200,200,0.9);
  margin-bottom: 5vh;
}
#spanni{
  font-size: 3rem;
  padding-left: 0.4rem;
  padding-right: 0.5rem;
  font-weight: 700;
  margin-bottom: -5rem;
  color: red;
}

.wrap{
  min-width: 30rem;
  max-width: 30rem;
  max-height: 50vh;
  padding: 1rem;
  margin: 0 auto;
  font-size: 2vh;
  font-family: monospace, 'Orbitron', sans-serif;    
}




@media only screen and (min-width: 1900px) {

 html, body{
   font-size: 20px;
 }
}

@media only screen and (min-width: 4000px) {

  html, body{
    font-size: 36px;
  }
 }
 
.box {
  width: 42rem;
  overflow: auto;
  scrollbar-color: #222;
  max-height: 15rem;
  min-height: 15rem;
  padding: 1rem;
  overflow-x: hidden;
  --moz-scrollbar-color: red;
  scrollbar-width: thin;  
  scrollbar-color: #222d #2224;   
}
´
 ::-webkit-scrollbar{
   width: 12px;
 }
::-webkit-scrollbar-track {
  background: #2224;
}
::-webkit-scrollbar-thumb {
  background-color: #222d;
  border-radius: 20px;
  border: 3px solid #2224;
}
.rivitbox {
  margin-left: 3.5rem;
  font-size: 1rem;
  padding: 0.2rem;
  width: 30rem;
  color: #777;
  text-align: left;
}
.tyyppi {
  float: left;
  color: #777;
  min-width: 6rem;
  max-width: 6rem;
}
.tyyppi_tehot {
  float: left;
  min-width: 1rem;
  max-width: 1rem;
}
.tyyppi_info {
  float: left;
  min-width: 2rem;
  max-width: 2rem;
  margin-right: 2rem;
  text-align: center;
}

.tyyppi_pvm {
  float: left;
  min-width: 6rem;
  max-width: 6rem;
  margin-right: 2.5rem;
  text-align: center;
}
