.inputwrap{
font-size: 0.8rem;
  width: 25rem;
margin-left:1.5rem;
margin-top: 1rem;
padding: 3rem;
background: linear-gradient(to right,#2226,#2220);
}

input {
  color: #222;
  margin-bottom: 0.5rem;
  width: 9rem;
  padding: 0rem;
  background: #eee;
}
select{
  margin-bottom: 0.5rem;
}
form {
  
  color: #eee;
  text-align: left;
  min-width: 10rem;
}
.labelbox {
  padding: 0rem;
  min-width: 10rem;
  float: left;
}
label {
  color: #777;
  text-shadow: 1px 1px rgba(0, 255, 255, 0.1);
  padding: 1rem 3rem 0 0;
  border-radius: 1rem 1rem 0 0;
  min-width: 20rem;
}

.buttonbox{

}
button{
  width: 9rem;
  padding: 0.2rem;
  margin-right: 1rem;
  font-size: 0.8rem;
  border: 0.2rem groove;
  text-shadow: 0 0 0.9rem #fff;
  font-weight: 700;
  background: linear-gradient(270deg, #eee8, #9993 );
}

.box {
  min-height: 20rem;
  width: 30rem;
}

.recharts-layer{
  background: pink;
} 

.recharts-rectangle{
width: 0.1rem;
}

.baarit{
  width: 30rem;
}

h1{
  padding-top: 4rem;
  padding-bottom: 4rem;
  padding-left: 1.5rem;
}

.App-header{
  
  background: linear-gradient(to right,#2220, #2229,#2220);
  box-shadow: 0 0 2rem #fff;
    -webkit-clip-path: polygon(
      0 0, /* left top */
      100% 0, /* right top */ 
      100% 100%, /* right bottom */
      0 80% /* left bottom */
    );
            clip-path: polygon(
      0 0, /* left top */
      100% 0, /* right top */ 
      100% 100%, /* right bottom */
      0 80% /* left bottom */
    );
  
}

.menu {
  display: inline-block;
  margin-top: 10vh;
  color: red;
  background: none;
  border-top: 0.4rem groove orange;
  box-shadow: -0rem -0.15rem 0  0 #ddd;
  margin-bottom: 7rem;
  min-width: 30rem;
}

.nappi {
  border-radius: 1rem;
  padding-top: 0.3rem;
  border: solid 0.2rem #222;
  transform: scale(0.8, 0.9) translate(0, -1.8rem);
  background: linear-gradient(90deg,#fffa, #fffd, #fffd, #fffa);
  font-weight: 700;
  color: #222;
  text-align: center;
  font-size: 1.5rem;
  margin-left: 2rem;
  width: 7rem;
  float: left;
}

